@font-face {
    font-family: bs;
    src: url(../fonts/bs.woff2);
}

@font-face {
    font-family: bs_light;
    src: url(../fonts/bs_light.woff2);
}

@font-face {
    font-family: Fredoka One;
    src: url(../fonts/freedoc.woff2);
}

@font-face {
    font-family: Nunito;
    src: url(../fonts/nunito.woff2);
}

:root {
    --primary: #fc9144;
    --secondary: #f7d020;
    --pink: #fa3c6a;
    --white: #fff;
    --dark: #000;
    --font-family-fredoka_one: "Fredoka One", Helvetica;
    --font-family-nunito: "Nunito", Helvetica;
}

* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    font-family: var(--font-family-nunito), 'Roboto', sans-serif;
}

html,
body {
    width: 100%;
    max-width: 100%;
    overflow-x: hidden;
    min-height: 100vh;
    scroll-behavior: smooth;
}

#root {
    min-height: 100vh;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
}

::-moz-selection {
    background-color: var(--primary);
    color: var(--white);
}

::selection {
    background-color: var(--primary);
    color: var(--white);
}

::-webkit-scrollbar {
    width: 8px;
    height: 4px;
    background: var(--white);
}

::-webkit-scrollbar-thumb {
    background-color: var(--secondary);
}


/*  */

.cs_container {
    max-width: 1250px;
    margin: 0 auto;
    padding: 0 18px;
}

.cs_row {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

.flex_column {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
}

.justify-content-between {
    -webkit-box-pack: between;
    -ms-flex-pack: between;
    justify-content: between;
}

.justify-content-center {
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}

.justify-content-start {
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start
}

.justify-content-end {
    -webkit-box-pack: end;
    -ms-flex-pack: end;
    justify-content: flex-end
}

.align-items-center {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.align-items-start {
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
}

.align-items-end {
    -webkit-box-align: end;
    -ms-flex-align: end;
    align-items: flex-end;
}

.cs_col_50 {
    width: 50%;
}

.w_100,
.cs_col_100 {
    width: 100%;
}


/*  */

.cs_btn {
    border-radius: 50px;
    border: none;
    outline: none;
    padding: 9px 40px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    max-width: 100%;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    text-decoration: none;
    font-size: 25px;
    color: var(--white);
    background-color: var(--primary);
    -webkit-transition: .4s transform linear;
    -o-transition: .4s transform linear;
    transition: .4s transform linear;
    /* font-weight: 600; */
    border: 2px solid var(--white);
    font-family: bs;
}

.cs_btn:hover {
    -webkit-transform: scale(1.04);
    -ms-transform: scale(1.04);
    transform: scale(1.04);
}

.bg_pr {
    background-color: var(--secondary)
}

.bg_sec {
    background-color: var(--secondary)
}

.text_dark {
    color: var(--dark);
}

.text_light {
    color: var(--white);
}


/*  */

.loader-wrapper {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    z-index: 111;
    background-color: rgba(0, 0, 0, 0.234);
}

.loader {
    border: 5px solid var(--white);
    border-top: 5px solid var(--primary);
    border-radius: 50%;
    width: 70px;
    height: 70px;
    -webkit-animation: spin 0.5s linear infinite;
    animation: spin 0.5s linear infinite;
    overflow: hidden;
    position: relative;
}

.loader-wrapp {
    margin: 0 auto;
    background-color: var(--secondary);
    outline-offset: -3px;
    position: relative;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    overflow: hidden;
}

.loader-logo {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.loader-logo img {
    max-width: 80%;
    margin-top: 7px;
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}


/* go to top */

.go-to-top {
    position: fixed;
    bottom: 9px;
    right: 9px;
    background-color: var(--primary);
    color: #fff;
    border-radius: 12px;
    width: 40px;
    height: 40px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    cursor: pointer;
    z-index: 999;
    border: 2px solid var(--white);
    -webkit-box-shadow: -3px 5px 15px rgba(0, 0, 0, 0.329);
    box-shadow: -3px 5px 15px rgba(0, 0, 0, 0.329);
    -webkit-transition: -webkit-transform 0.3s linear;
    transition: -webkit-transform 0.3s linear;
    -o-transition: transform 0.3s linear;
    transition: transform 0.3s linear;
    transition: transform 0.3s linear, -webkit-transform 0.3s linear;
    visibility: hidden;
    -webkit-transform: translateX(150%);
    -ms-transform: translateX(150%);
    transform: translateX(150%);
}

.show-top {
    visibility: visible;
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0);
}

.d-none {
    display: none;
}

.text_fredoc {
    font-family: var(--font-family-fredoka_one);
}

.img_fluid {
    max-width: 100%;
    width: auto;
    -o-object-fit: contain;
    object-fit: contain;
}

.sec_heading_wrapper {
    width: 100%;
    text-align: center;
    padding: 15px 0 25px 0;
}

.sec_heading {
    font-size: 3.9rem;
    -webkit-text-fill-color: var(--primary);
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: var(--dark);
}

section {
    overflow: hidden;
}


/* intro */

#home {
    background-color: var(--secondary)
}

#home .cs_row {
    padding: 74px 17px 50px 17px;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    min-height: 370px;
}

.intro_content {
    max-width: 400px;
}

.intro_content h1 {
    color: var(--dark);
    font-size: 4.3rem;
    font-weight: 500;
}

.intro_content p {
    padding: 25px 0;
    font-weight: 300;
    font-size: 19px;
}

.mint_avail_text {
    font-size: 3.2rem;
}

.intro_content .cs_btn.mint_btn {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    border: 5px solid var(--dark);
    margin: 17px 0;
    width: 300px;
    font-size: 31px
}

.cs_btn select {
    border-radius: 50px;
    border: 5px solid var(--dark);
    margin-top: 7px;
    background-color: var(--secondary);
    font-size: 17px;
    overflow: hidden;
    text-align: center;
}

.intro_video_wrapper {
    width: 100%;
    padding: 50px 15px;
    text-align: center;
}

.intro_video_wrapper video {
    width: 100%;
    -o-object-fit: cover;
    object-fit: cover;
}


/* about */

#about {
    padding: 75px 17px;
    color: #bb6423
}

.about_content h1 {
    font-size: 4.1rem;
}

.about_content p {
    font-size: 1.7rem;
    font-weight: 400;
    padding-top: 25px;
}

.about_img_wrapper {
    text-align: center;
}

.about_img_wrapper img {
    max-width: 520px
}


/* faqs */

#faqs {
    padding: 25px 0;
}

#faqs .cs_container {
    padding: 50px 100px !important;
    text-align: center;
}

.faq_wrapper {
    margin: 22px auto;
    overflow: hidden;
    -webkit-transition: .4s linear;
    -o-transition: .4s linear;
    transition: .4s linear;
    -webkit-backdrop-filter: blur(2px) saturate(80%);
    backdrop-filter: blur(2px) saturate(80%);
}

.faq_qs {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: flex-start;
    padding: 14px 16px 12px 16px;
    cursor: pointer;
    border: 2px solid var(--dark);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border-radius: 12px;
}

.faq_qs h6 {
    font-weight: 600;
    font-size: 26px;
    width: 100%;
    text-align: center;
}

.faqs_answer {
    height: 0;
    overflow: hidden;
    padding: 0 15px;
    font-weight: 400;
    -webkit-transition: .4s linear;
    -o-transition: .4s linear;
    transition: .4s linear;
    font-size: 20px;
    text-align: left;
}

.active_faq .faqs_answer {
    padding: 15px;
    height: auto;
}

.faq_arrow * {
    font-size: 16px !important;
}

.faq_arrow {
    background-color: var(--secondary);
    border-radius: 50%;
    width: 24px;
    height: 24px;
    overflow: hidden;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-transition: -webkit-transform 0.3s linear;
    transition: -webkit-transform 0.3s linear;
    -o-transition: transform 0.3s linear;
    transition: transform 0.3s linear;
    transition: transform 0.3s linear, -webkit-transform 0.3s linear;
    padding: 4px;
    color: #fff;
}

@media screen and (max-width:768px) {
    #faq {
        margin-top: 0;
        padding: 8px 0;
    }
    #faqs .cs_container {
        padding: 30px 14px !important;
    }
    .sec_heading_wrapper {
        padding: 4px 0 7px 0;
    }
    .sec_heading {
        font-size: 3rem;
    }
    /*  */
    #home {
        background-color: var(--secondary)
    }
    #home .cs_row {
        padding: 30px 0 0 0;
        -webkit-box-align: start;
        -ms-flex-align: start;
        align-items: flex-start;
        min-height: 240px;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
    }
    .cs_col_50 {
        width: 100%;
    }
    .intro_content {
        max-width: 90%;
        margin: 0 auto;
        text-align: center;
    }
    .intro_content h1 {
        font-size: 3rem;
    }
    .intro_content p {
        padding: 18px 0;
        font-weight: 300;
        font-size: 17px;
    }
    .mint_avail_text {
        font-size: 2.4rem;
        margin: 0 auto;
        text-align: center;
        width: 100%;
    }
    .intro_content .cs_btn.mint_btn {
        margin: 14px auto;
        width: 82%;
        font-size: 25px
    }
    .cs_btn select {
        margin-top: 5px;
    }
    .intro_video_wrapper {
        padding: 32px 15px 10px 15px;
    }
    /*  */
    /* about */
    #about {
        padding: 45px 17px 15px 17px;
    }
    .about_row {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column
    }
    .about_content {
        text-align: center;
    }
    .about_content h1 {
        font-size: 2.7rem;
    }
    .about_content p {
        font-size: 1.2rem;
        padding-top: 18px;
    }
    .about_img_wrapper {
        width: 100%;
        padding-top: 50px;
    }
    .about_img_wrapper img {
        max-width: 94%;
        margin: 0 auto;
    }
}

@media screen and (max-width:540px) {
    .faqs_answer p,
    .faq_qs h6 {
        font-size: 17px;
    }
}

@media screen and (max-width:340px) {
    .intro_content h1 {
        font-size: 2.5rem;
    }
    .intro_content p {
        padding: 14px 0;
        font-weight: 300;
        font-size: 15px;
    }
    .mint_avail_text {
        font-size: 2.1rem;
    }
}