.counter-wrapper {
    width: 100%;
    position: absolute;
    top: 56%;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
    right: 0;
    z-index: 2;
}

.launch-date {
    margin: 0 auto;
    text-align: center;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    max-width: 100%;
    margin-bottom: 8px;
}

.launch-date h3 {
    font-size: 2.7rem;
    text-shadow: 3px 7px 4px rgba(0, 0, 0, 0.39);
    margin: 20px 0 15px 0;
}

.hero-counter {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    margin: 0 auto;
}

.counter {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    background: var(--primary);
    border-radius: 4px;
    padding: 9px 3px 4px 3px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.counter-item {
    margin: 0 4.5px;
    text-align: center;
}

.counter--label {
    font-weight: 800;
    font-size: 14px;
    text-transform: uppercase;
}

.counter--value {
    background: rgb(4, 9, 26);
    border-radius: 4px;
    width: 65px;
    height: 80px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    margin: 3px auto 0 auto;
}

.counter--value h5 {
    font-weight: 900;
    font-size: 35px;
    color: rgb(243, 239, 239);
}

@media (max-width: 550px) {
    .launch-date {
        margin: 0 auto;
        text-align: center;
        width: -webkit-max-content;
        width: -moz-max-content;
        width: max-content;
        margin-bottom: 8px;
    }
    .launch-date h3 {
        font-size: 25px;
        margin-top: 15px;
        margin-bottom: 15px;
        text-shadow: 3px 7px 7px rgba(0, 0, 0, 0.151);
    }
    .hero-counter {
        width: -webkit-max-content;
        width: -moz-max-content;
        width: max-content;
        margin: 0 auto;
    }
    .counter--label {
        font-size: 11px;
    }
    .counter--value {
        width: 50px;
        height: 70px;
    }
    .counter--value h5 {
        font-size: 27px;
    }
}