.web_footer {
    background-color: var(--pink);
    margin-top: auto;
    padding: 50px;
    color: var(--white);
}

.web_footer * {
    font-family: bs_light, cursive !important;
}

.footer_logo {
    width: 30%;
    min-width: 150px;
    margin-bottom: 20px;
}

.footer_logo img {
    max-width: 85%;
}

.footer_links {
    padding: 8px 10px;
    min-width: 120px;
}

.footer_links ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.footer_links ul li {
    margin: 4px 0;
    cursor: pointer;
    font-size: 26px;
}

.footer_links ul li:hover span {
    text-decoration: underline;
}

.footer_intro {
    padding: 15px;
    color: rgba(255, 255, 255, .5);
    max-width: 325px;
    font-size: 21px;
}

.footer_right {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
}

.footer_right .social_icons_wrapper a:hover {
    text-decoration: none;
}

.footer_right .social_icons_wrapper a {
    font-size: 24px;
    margin: 0 6px;
    padding: 0 6px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    font-weight: 600;
    color: #222;
}

.footer_right .social_icons_wrapper .social_btn {
    margin-right: 7px;
    color: var(--pink);
    background-color: var(--white);
}

.footer_copy_right {
    padding-top: 32px;
}

.footer_copy_right p {
    padding: 0 10px;
    max-width: 240px;
    font-size: 24px;
    color: var(--white);
    text-align: center;
}

.footer_copy_logo {
    max-width: 200px;
    padding-right: 7px;
}

.footer_copy_logo img {
    max-width: 100%;
    -o-object-fit: contain;
    object-fit: contain;
}

@media (max-width: 992px) {
    .footer_row {
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
    }
