@media (max-width: 520px) {
    .web_footer {
        padding: 40px 16px 25px 16px;
    }
    .footer_logo {
        -webkit-box-ordinal-group: 2;
        -ms-flex-order: 1;
        order: 1;
        width: 100%;
        text-align: center;
    }
    .footer_right {
        -webkit-box-ordinal-group: 5;
        -ms-flex-order: 4;
        order: 4;
        width: 100%;
    }
    .footer_right .social_btn {
        width: 30px;
        height: 30px;
        font-size: 18px;
    }
    .footer_right .social_icons_wrapper a {
        font-size: 15px;
        margin: 0 3px;
    }
    .footer_intro {
        -webkit-box-ordinal-group: 3;
        -ms-flex-order: 2;
        order: 2;
        max-width: 88%;
        font-size: 18px;
        text-align: center;
    }
    .footer_links {
        min-width: 100%;
        padding: 15px;
        text-align: center;
        -webkit-box-ordinal-group: 4;
        -ms-flex-order: 3;
        order: 3;
    }
    .footer_links ul {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
    }
    .footer_links ul li {
        margin: 0 7px;
        font-size: 19px;
    }
    .footer_links ul li span {
        text-decoration: underline;
        padding: 6px;
    }
    .footer_copy_right {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
    }
    .footer_copy_logo {
        margin-bottom: 22px;
        max-width: 150px;
    }
    .footer_copy_right p {
        max-width: 88%;
        font-size: 18px;
    }
}

@media (max-width: 340px) {
    .footer_right .social_btn img {
        max-width: 64%;
    }
    .footer_right .social_btn {
        width: 27px;
        height: 27px;
        font-size: 17px;
    }
    .footer_right .social_icons_wrapper a {
        font-size: 13px;
        padding: 0 3px;
    }
    .footer_intro {
        max-width: 94%;
    }
    .footer_copy_right p {
        font-size: 16px;
        width: 100%;
        text-align: center;
    }
}