.web-header {
    width: 100%;
    background-color: var(--secondary);
    overflow: hidden;
    -webkit-transition: 0.3s ease-in-out !important;
    -o-transition: 0.3s ease-in-out !important;
    transition: 0.3s ease-in-out !important;
    z-index: 999;
    -webkit-transition: all 0.6s ease;
    -o-transition: all 0.6s ease;
    transition: all 0.6s ease;
}

@-webkit-keyframes comeFromTOp {
    from {
        -webkit-transform: translateY(-70px);
        transform: translateY(-70px);
    }
    to {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}

@keyframes comeFromTOp {
    from {
        -webkit-transform: translateY(-70px);
        transform: translateY(-70px);
    }
    to {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}

.header-container {
    margin: 0 auto;
    padding: 20px 18px 15px 18px !important;
    min-height: 58px;
    max-height: 110px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.web-header.affix .header-container {
    padding: 8px 18px 15px 8px !important;
}

.web-header.affix {
    position: fixed;
    top: 0;
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0);
    -webkit-box-shadow: 5px 4px 15px rgba(0, 0, 0, 0.281);
    box-shadow: 5px 4px 15px rgba(0, 0, 0, 0.281);
    -webkit-animation: comeFromTOp 0.6s;
    animation: comeFromTOp 0.6s;
}

.logo-part {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.logo-part img {
    cursor: pointer;
    width: 100%;
    max-width: 100%;
    -o-object-fit: contain;
    object-fit: contain;
}

.headLogo,
.logo-part img.headLogo {
    max-width: 85px;
}

.logo-part img.headLogo {
    margin-right: 12px;
}

.header_right {
    margin-left: auto;
}

.header_right,
.social_icons_wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.social_icons_wrapper .social_btn {
    border-radius: 50%;
    cursor: pointer;
    background-color: var(--primary);
    width: 40px;
    height: 40px;
    margin: 6px 4px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    color: var(--white);
    font-size: 24px;
    -webkit-transition: -webkit-transform .3s ease;
    transition: -webkit-transform .3s ease;
    -o-transition: transform .3s ease;
    transition: transform .3s ease;
    transition: transform .3s ease, -webkit-transform .3s ease;
}

.header_right .social_icons_wrapper {
    padding-right: 25px;
}

.social_btn:hover {
    -webkit-transform: scale(1.04);
    -ms-transform: scale(1.04);
    transform: scale(1.04);
}

@media (max-width: 838px) {
    .logo-part img:not(.headLogo) {
        display: none;
    }
    .headLogo,
    .logo-part img.headLogo {
        max-width: 65px;
        min-width: 38px;
    }
    .header_right .cs_btn {
        font-size: 16px;
        padding: 6px 15px;
    }
    .header_right .social_icons_wrapper {
        padding-right: 10px;
    }
    .social_icons_wrapper .social_btn {
        width: 30px;
        height: 30px;
        margin: 6px 2.3px;
        font-size: 19px;
    }
    .social_icons_wrapper .social_btn img {
        max-width: 66%;
    }
    .web-header.affix .header-container,
    .header-container {
        padding: 8px 10px 8px 10px !important;
        min-height: 45px;
        max-height: none;
    }
}

@media (max-width: 355px) {
    .header_right .cs_btn {
        font-size: 12px;
        padding: 4px 9px;
    }
    .header_right .social_icons_wrapper {
        padding-right: 5px;
    }
    .social_icons_wrapper .social_btn {
        width: 26px;
        height: 26px;
        margin: 4px 2px;
        font-size: 16px;
    }
}